
header {
    background: url(../images/header/bg.png) no-repeat top center #458ed1;
    background-size: cover;
    padding: 8px 13px;
    .wrapper {
        display: flex;
        
        justify-content: space-between;
        align-items: center;
        @media (max-width:720px) {
            flex-direction: column;
            justify-content: center;
            .logo {
                margin-bottom: 5px;
            }
        }
    }
}
.head {
    &_sale {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        
        text-align: center;
        @media (max-width:500px) {
            flex-direction: column;
        }
        &_block {
            @media (max-width:500px) {
                margin: 5px 0;   
            }
            display: flex;
            align-items: center;
            &_left {
                color: #1f3756;
                display: flex;
                align-items: stretch;
                font-weight: 700;
                margin-right: 10px;
                div {
                    &:first-child {
                        background: #ffff28;
                        border-radius: 4px;
                            padding: 5px 18px 0 18px;
                        
                        font-size: 43px;
                        line-height: 37px;
                        
                        span {
                            font-size: 19px;
                            text-transform: uppercase;
                            line-height: 31px;
                        }
                    }
                    &:last-child {
                        background: -moz-linear-gradient(top, rgba(252,252,252,0) 0%, rgba(221,221,221,0.3) 100%);
                        background: -webkit-linear-gradient(top, rgba(252,252,252,0) 0%,rgba(221,221,221,0.3) 100%);
                        background: linear-gradient(to bottom, rgba(252,252,252,0) 0%,rgba(221,221,221,0.3) 100%);
                        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00fcfcfc', endColorstr='#4ddddddd',GradientType=0 );
                        
                        color: #fff;
                        text-align: left;
                        
                        border-radius: 0 4px 4px 0;
                        padding: 16px 12px 15px 12px;
                        text-transform: uppercase;
                        font-size: 18px;
                    }
                }
            }
            &_right {
                min-width: 175px;
                text-align: center;
                background: url(../../images/header/sale.png) repeat-y top center;
                justify-content: center;
                flex-direction: column;
                padding-top: 7px;
                color: #1f3756;
                font-size: 34px;
                font-weight: 700;
                margin-right: 21px;
                span {
                    font-size: 22px;
                    line-height: 15px;
                    position: relative;
                    &:after {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 50%;
                        height: 2px;
                        left: -2px;
                        right: -2px;
                        background: #1f3756;
//                        margin-top: 0px;
                    }
                }
                b {
                    font-size: 18px;
                    
                }
            }
        }
        span {
            display: block;
            
        }
    }
}
section {
    background: rgb(36,82,140);
    background: -moz-radial-gradient(center, ellipse cover, rgba(36,82,140,1) 0%, rgba(27,34,43,1) 100%);
    background: -webkit-radial-gradient(center, ellipse cover, rgba(36,82,140,1) 0%,rgba(27,34,43,1) 100%);
    background: radial-gradient(ellipse at center, rgba(36,82,140,1) 0%,rgba(27,34,43,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#24528c', endColorstr='#1b222b',GradientType=1 );
    .block {
        &_right {
            padding-top: 49px;
            padding-bottom: 40px;
            width: 55%;
            @media (max-width:950px) {
                width: 100%;   
            }
        }
        &_left {
            width: 45%;
            img {
                height: 100%;
                display: block;
            }
            @media (max-width:950px) {
                display: none;
            }
        }
    }
    h1 {
        padding-left: 30px;
        font-size: 51px;
        font-weight: 700;
        text-transform: uppercase;
        color: #fff;
        @media (max-width:500px) {
            font-size: 28px;
        }
    }
    h2 {
        color: #ffff28;
        text-transform: uppercase;
        font-size: 22px;
        padding-left: 30px;
        margin-bottom: 30px;
        line-height: 34px;
        @media (max-width:500px) {
                
        }
        span {
            
            font-size: 32px;
            display: block;
            @media (max-width:500px) {
                font-size: 18px;
            }
        }
    }
    
}
.list {
    font-size: 17px;
    font-weight: 700;
    color: #fff;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        width: 18px;
        height: 140px;
        background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,0.4) 100%);
        background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,0.4) 100%);
        background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,0.4) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#66ffffff',GradientType=0 );
        
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 129px, 8px 100%, 0 133px);
        clip-path: polygon(0 0, 100% 0, 100% 129px, 8px 100%, 0 133px);
        bottom: calc( 100% + 10px )
    }
    img {
        margin-right: 11px;
    }
    div {
            margin-bottom: 19px;
    }
    
}
.section {
    &_first {
        background: #2670d1;
    }
    &_second {
        background: #ecf1f5;
    }
    &_fourth {
        background: #0072d5;
        .wrapper {
            background: #0072d5 url(../images/fourth/bg.png) no-repeat top center;
            background-size: cover;
        }
    }
    &_five {
        background: #ecf1f5;
    }
    &_six {
        background: #111b27 url(../images/six/bg.png) no-repeat top center;
        background-size: cover;
    }
    &_seven {
        background: #2670d1 url(../../images/seven/bg.png) no-repeat top center;
        background-size: cover;
    }
    &_eight {
        background: #ecf1f5;
    }
}
.first {
    color: #fff;
    font-size: 20px;
    position: relative;
    z-index: 2;
    
    .wrapper {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        @media (max-width:930px) {
            flex-wrap: wrap;   
        }
        @media (max-width:580px) {
            flex-direction: column;
        }
    }
    span {
        text-transform: uppercase;
    }
    &_sert {
        font-size: 12.55px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        text-align: center;
        font-weight: 900;
         
        padding: 12px 0;
        
        min-width: 291px;
        
        span {
            display: block;
        }
    }
    &_prod {
        img {
            margin: -55px 39px -45px 25px;
            @media (max-width:930px) {
                margin-top: 0;
                margin-bottom: 0;
            }
        }
        
    }
    &_text {
        font-family: 'Open Sans', sans-serif;
        font-weight: 600;
        font-style: italic;
        @media (max-width:930px) {
            min-width: 100%;
            text-align: center;
            margin-bottom: 20px;
            padding-top: 15px;
        }
        span {
            font-family: 'Raleway', sans-serif;
            font-style: normal;
            text-transform: uppercase;
            font-weight: 600;
            font-size: 24px;
        }
    }
}
.second {
    .flip-clock-wrapper {
        text-align: center;
        position: relative;
        width: auto;
        min-width: 297px;
    }
    &_top {
        h2 {
            font-size: 32px;
            color: #3278d3;
            padding-top: 65px;
            text-transform: uppercase;
            margin-bottom: 42px;
        }
        .block {
            &_left {
                width: 44%;
            }
            &_rigth {
