            background: #eecc0f;
            width: 100%;
            border-radius: 200px 200px 7px 7px / 12px 12px 7px 7px;
            padding: 20px 20px 25px 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width:700px) {
                flex-direction: column;
            }
            @media (max-width:600px) {
                padding: 20px 0;
            }
        }
        a {
//            display: inline-block;
//            padding: 17px;
//            border: 2px solid #44443e;
//            background: #3f98df;
//            text-decoration: none;
//            color: #fff;
//            border-radius: 3px;
        }
        span {
            text-align: right;
            @media (max-width:700px) {
                br {
                    display: none;
                }
            }
        }
    }
    &_bottom {
        color: #1f3756;
        font-size: 18px;
        line-height: 32px;
        font-family: 'Open Sans', sans-serif;
        font-style: italic;
        
        padding-bottom: 60px;
        span {
            font-style: normal;
            text-transform: uppercase;
            font-family: 'Raleway', sans-serif;
            font-weight: 600;
            color: #558cd9;
        }
        
        .block {
            &_left {
                width: 57%;
            } 
            &_rigth {
                width: 43%;
                padding-left: 20px;
                img {
                    width: 100%;
                    max-width: 396px;
                }
            }
            @media (max-width:830px) {
                &_left ,
                &_rigth {
                    width: 100%;   
                    text-align: center;
                    order: 2;
                }
                &_rigth {
                    order: 1;
                }
            }
        }
        &.block {
            &_lr {
                @media (max-width:830px) {
                    flex-direction: column;
                } 
            }
        }
    }
    
}
