            max-width: 255px;
            margin: 0 auto;
        }
    }
    
}
.form {
    background: #2670d1;
    border-radius: 7px;
    overflow: hidden;
    margin-top: 45px;
    margin-left: 100px;
    margin-right: 100px;
    @media (max-width:1060px) {
        margin-left: 15px;
        margin-right: 15px;
    }
    @media (max-width:440px) {
        margin-left: -15px;
        width: calc( 100% + 30px );
        margin-right: -15px;
    }
    
    &.block {
        &_lr {
            @media (max-width:870px) {
                flex-direction: column;
            }
        }
    }
    & > .block {
        &_right {
            color: #1f3756;
            background: #fff;
            text-align: left;
            padding: 40px 22px;
            width: 34%;
            position: relative;
            @media (max-width:870px) {
                width: 100%;   
            }
            &:after {
                content: '';
                display: block;
                top: 0;
                bottom: 0;
                width: 35px;
                left: -34px;
                background: #fff;
                position: absolute;
                -webkit-clip-path: polygon(100% 0, 100% 100%, 0 50%);
                clip-path: polygon(100% 0, 100% 100%, 0 50%);
            }
            b {
                display: block;
                margin-bottom: 10px;
            }
        }
        &_left {
            width: 68%;
            background: rgb(38,112,209);
            background: -moz-linear-gradient(-45deg, rgba(38,112,209,1) 2%, rgba(58,170,233,1) 50%, rgba(38,112,209,1) 94%);
            background: -webkit-linear-gradient(-45deg, rgba(38,112,209,1) 2%,rgba(58,170,233,1) 50%,rgba(38,112,209,1) 94%);
            background: linear-gradient(135deg, rgba(38,112,209,1) 2%,rgba(58,170,233,1) 50%,rgba(38,112,209,1) 94%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#2670d1', endColorstr='#2670d1',GradientType=1 );
            
            padding: 20px 50px 20px 25px;
            @media (max-width:870px) {
                width: 100%; 
                padding: 20px 25px 20px 25px;
            }
            .block {
                &_right {
                    min-width: 60%;
                }
            }
            &.block {
                &_lr {
                    @media (max-width:650px) {
                        flex-direction: column;   
                        & > * {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }
    .block {
        &_left {
            text-align: center;
        }
    }
    label {
        display: block;
    }
    input {
        border: 2px solid #44443e;
        padding: 15px 10px;
        font-size: 16px;
        margin-bottom: 27px;
        border-radius: 4px;
        width: 100%;
    }
    button {
        display: block;
        text-align: center;
        width: 100%;
        font-size: 24px;
    }
    .sale {
        width: 110px;
        height: 110px;
        border-radius: 50%;
        background: #fef927;
        color: #1f3756;
        font-size: 43px;
            line-height: 40px;
        font-weight: 700;
        margin: 0 auto;
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        span {
            font-size: 19px;
            line-height: 25px;
        }
        & + img {
            margin-top: -36px;
        }
    }
    .now {
        font-size: 30px;
        text-transform: uppercase;
        font-weight: bold;
        @media (max-width:650px) {
            text-align: center!important;   
        }
    }
    .count {
       .now {
            font-size: 24px;
            text-align: right;
        }
    }
    .flip-clock-wrapper {
    text-align: center;
    position: relative;
    width: 100%;
    min-width: 330px;
    max-width: 330px;
    margin: 0 auto;
        @media (max-width:340px) {
            margin-left: -20px;   
        }
    }
}
.btn {
    display: inline-block;
            padding: 17px;
            border: 2px solid #44443e;
            background: #3f98df;
            text-decoration: none;
            color: #fff;
            border-radius: 3px;
    text-transform: uppercase;
}

.ticket {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    margin: 30px 12px;
    &_left {
        width: 50%;
        background: #e6c300;
        font-size: 22px;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 67px;
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            width: 4px;
            left: -4px;
            bottom: 0;
            top: 0;
            background: url(../images/footer/bef.png) repeat-y 0 0;
        }
        span {
            position: relative;
            &:after {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                height: 2px;
                left: -2px;
                right: -2px;
                background: #fff;
            }
        }
    }
    &_right {
        width: 50%;
        background: #fbfb3c;
        color: #1f3756;
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        &:before {
            content: '';
            display: block;
            position: absolute;
            width: 4px;
            left: 100%;
            bottom: 0;
            top: 0;
            background: url(../images/footer/after.png) repeat-y 0 0;
        }
        b {
            font-size: 34px;
        }
    }
}




